import { Breadcrumb, SimpleCard } from 'app/components';
import { Row, Col, Button, InputGroup, Form, Card } from 'react-bootstrap';
import { styled } from '@mui/system';
import { Icon, Box, Autocomplete, TextField, FormControl } from '@mui/material';
import React, { useState } from 'react';
import SubtitlesIcon from '@mui/icons-material/Subtitles';
import { useNavigate, useLocation } from 'react-router-dom';
import axios from 'axios';

const Container = styled('div')(({ theme }) => ({
    margin: '30px',
    [theme.breakpoints.down('sm')]: { margin: '16px' },
    '& .breadcrumb': {
        marginBottom: '30px',
        [theme.breakpoints.down('sm')]: { marginBottom: '16px' },
    },
}));


const EditFormCard = () => {
    const location = useLocation();
    console.log(location.state)
    const [id, setId] = useState(location.state.id)
    const [name, setName] = useState(location.state.name);
    const [profileName, setProfileName] = useState(location.state.profileRole);
    const [mobileNo1, setMobileNo1] = useState(location.state.mobileNo1);
    const [mobileNo2, setMobileNo2] = useState(location.state.mobileNo2);
    const [address, setAddress] = useState(location.state.address);
    const [email, setEmail] = useState(location.state.email);
    // const [name, setName] = useState(theEditCard.name);
    // const [profileName, setProfileName] = useState(theEditCard.profileRole);
    // const [mobileNo1, setMobileNo1] = useState(theEditCard.mobileNo1);
    // const [mobileNo2, setMobileNo2] = useState(theEditCard.mobileNo2);
    // const [address, setAddress] = useState(theEditCard.address);
    // const [email, setEmail] = useState(theEditCard.email);
    const [dob, setDob] = useState('');
    const [cardOptions, setCardOptions] = useState('Navy Blue');
    const [myOptions, setMyOptions] = useState({});

    const options = [
        { id: 1, name: 'Navy Blue', colorHeading1: 'white', colorHeading2: 'orange', image: "https://i.pinimg.com/600x315/c5/b3/d2/c5b3d2da72ad50f5f196f64d2be24019.jpg" },
        { id: 2, name: 'Yellow Blue', colorHeading1: 'white', colorHeading2: 'orange', image: "data:image/jpeg;base64," },
        { id: 3, name: 'Red White', colorHeading1: 'white', colorHeading2: 'orange', image: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSEvu0b5x7D77Bm4BLMoPkKaE5x-K3gkezUwg&usqp=CAU" },
        { id: 4, name: 'Black White', colorHeading1: 'white', colorHeading2: 'orange', image: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQE7HQGs1EsdF09LJmurIO0N-OrtZLXkW5UyQ&usqp=CAU" },
        { id: 5, name: 'Blue Golden', colorHeading1: 'white', colorHeading2: 'orange', image: "https://t4.ftcdn.net/jpg/03/98/35/91/360_F_398359128_fCeLDN5UOrirgsgmEW81EYmUeVmJpDLL.jpg" },
        { id: 6, name: 'Dark Mode', colorHeading1: 'white', colorHeading2: 'orange', image: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSixxt2HLLepK11oMCeBmgn33xly6nTzWpQ-pozvO3HGyZEROmjXYOVxC4s7rBl1uugbfo&usqp=CAU" },

    ];

    const nameOption = ['Navy Blue', 'Yellow Blue', 'Red White', 'Black White', 'Blue Golden', 'Dark Mode']

    const saveCard = (name) => {
        setCardOptions(name)
        for (var i = 0; i < options.length; i++) {
            if (options[i].name == name) {
                setMyOptions(options[i])
            }
            console.log(name)
        }

    }
    const navigate = useNavigate();
    const changePage = () => {
        navigate('/digitalCards');
    };
    const userCard = {
        _id: id,
        name: name,
        profileRole: profileName,
        mobileNo1: mobileNo1,
        mobileNo2: mobileNo2,
        email: email,
        address: address,
        status: 1,
        lastActive: null,
        createdBy: 1,
        updatedBy: 1
    }
    const postData = () => {
        const items = localStorage.getItem('accessToken');
        console.log(userCard);
        axios.post('http://213.136.72.177/cms/api/digitalCardUpsert', userCard,
            { headers: { "x-access-token": items } }
        );
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        postData();
        changePage();
    };

    return (
        <Container>
            <Box className="breadcrumb">
                <Breadcrumb
                    routeSegments={[
                        { name: 'Digital Card', path: '/digitalCards' },
                        { name: 'Add Digital Card' },
                    ]}
                />
            </Box>
            <Row>
                <Col>
                    <div style={{ position: "static", marginLeft: "227px", top: "110px", marginTop: "-10px" }}>
                        <Card style={{
                            borderRadius: '29px', height: "250px", width: "520px",
                            background: `url(${myOptions.image}) center/cover no-repeat`
                        }}>
                            {/* <Card.Img variant="top" src="" /> */}
                            <Card.Body className="text-center">
                                <Row>
                                    <Col>
                                        <h5>{userCard.name}</h5>
                                        <Row>
                                            <Col className='ml-3'>
                                                <Card.Text className="text-muted mb-4">
                                                    <p className="mx-2">{userCard.profileRole}</p>
                                                </Card.Text>
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col >
                                                <Icon color='primary' >phone</Icon>
                                                <span>{userCard.mobileNo1}/
                                                    {userCard.mobileNo2}
                                                </span>

                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col>
                                                <Icon color='primary' >mail</Icon>
                                                <span>{userCard.email}</span>

                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col>
                                                <Icon color='primary' >home</Icon>
                                                <span>{userCard.address}</span>

                                            </Col>
                                        </Row>
                                    </Col>
                                    <Col md="5">
                                        <h5>
                                            Boostock
                                        </h5>
                                        The Finance Advisory

                                        {/* <img width="180px"
                                            height="180px"
                                            sizes="12px"
                                            src="/assets/images/payment-card/boostock-logo.jpg" /> */}
                                    </Col>
                                </Row>
                            </Card.Body>
                        </Card>
                    </div>
                </Col>
            </Row>
            <Row>
                <Col>
                    <SimpleCard title="Fill Employee Detail's">
                        <Row>
                            <Col>
                                <Form.Label>Full Name</Form.Label>
                                <InputGroup>
                                    <InputGroup.Text id="basic-addon1">
                                        <Icon>person</Icon>
                                    </InputGroup.Text>
                                    <Form.Control
                                        onChange={(e) => setName(e.target.value)}
                                        value={name}
                                        required
                                        placeholder="Enter the Full Name"
                                    /></InputGroup>
                            </Col>
                            <Col>
                                <Form.Label>Profile</Form.Label>
                                <InputGroup>
                                    <InputGroup.Text id="basic-addon1">
                                        <SubtitlesIcon />
                                    </InputGroup.Text>
                                    <Form.Control
                                        required
                                        onChange={(e) => setProfileName(e.target.value)}
                                        value={profileName}
                                        placeholder="Enter the Profile Name"
                                    /></InputGroup>
                            </Col>
                        </Row>

                        <Row>
                            <Col>
                                <Form.Label>Personal Mobile</Form.Label>
                                <InputGroup>
                                    <InputGroup.Text id="basic-addon1">
                                        <Icon>phone</Icon>
                                    </InputGroup.Text>
                                    <Form.Control
                                        required
                                        onChange={(e) => setMobileNo1(e.target.value)}
                                        value={mobileNo1}
                                        placeholder="Enter the Personal Mobile"
                                    /></InputGroup>
                            </Col>
                            <Col>
                                <Form.Label>Alternate Mobile</Form.Label>
                                <InputGroup>
                                    <InputGroup.Text id="basic-addon1">
                                        <Icon>phone</Icon>
                                    </InputGroup.Text>
                                    <Form.Control
                                        required
                                        onChange={(e) => setMobileNo2(e.target.value)}
                                        value={mobileNo2}
                                        placeholder="Enter the Alternate Mobile"
                                    /></InputGroup>
                            </Col>
                        </Row>

                        <Row>
                            <Col>
                                <Form.Label>Email</Form.Label>
                                <InputGroup>
                                    <InputGroup.Text id="basic-addon1">
                                        <Icon>mail</Icon>
                                    </InputGroup.Text>
                                    <Form.Control
                                        required
                                        onChange={(e) => setEmail(e.target.value)}
                                        value={email}
                                        placeholder="Enter the Email Id"
                                    /></InputGroup>
                            </Col>
                            <Col>
                                <InputGroup>
                                    <Form.Label className="mt-1">Interested In</Form.Label>
                                </InputGroup>
                                <FormControl>
                                    <Autocomplete
                                        style={{ width: 470 }}
                                        freeSolo
                                        autoComplete
                                        autoHighlight
                                        value={cardOptions}
                                        options={nameOption}
                                        onChange={(e) => saveCard(e.currentTarget.innerHTML)}
                                        renderInput={(params) => (
                                            <TextField
                                                {...params}
                                                variant="outlined"
                                                label="Select the Interested Catalogue"
                                                size="small"
                                            />
                                        )}
                                    />
                                </FormControl>

                            </Col>
                        </Row>
                        <Row>
                            <Col>
                                <Form.Label>Address</Form.Label>
                                <InputGroup>
                                    <InputGroup.Text id="basic-addon1">
                                        <Icon>home</Icon>
                                    </InputGroup.Text>
                                    <Form.Control
                                        required
                                        onChange={(e) => setAddress(e.target.value)}
                                        value={address}
                                        placeholder="Enter your Address"
                                    /></InputGroup>
                            </Col>
                        </Row>
                    </SimpleCard>
                </Col>
            </Row>
            <Div className="mt-2">
                <Row>
                    <Col>
                        <Button variant="secondary" onClick={changePage}>
                            Cancel
                        </Button>
                        &nbsp;
                        <button type="button" className="btn btn-success" onClick={handleSubmit}>
                            Save
                        </button>
                    </Col>
                </Row>
            </Div>
        </Container >
    );
}
const Div = styled('div')(() => ({
    margin: '0px 0px 0px 441px',
}));
export default EditFormCard